import { Injectable } from '@angular/core';
import { EquipmentsMocks } from 'src/mocks/equipments.mocks';
import { Store } from '@ngrx/store';

@Injectable({
  providedIn: 'root'
})
export class EquipmentServiceService {

  constructor( 
      private equipmentsMocks : EquipmentsMocks,
      private store : Store<any>          ) { }
  
    getAllEquipments(){
      return this.equipmentsMocks.getEquipments();
    }
  
    getAllState(){
      return this.store.select('appReducer')
    }
  

}
